* {
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
}

.nodeContainer {
    margin: auto;
    width: 100vw;
    height: 100vh;
    background-color: gold;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding: 200px 0;
}

.row {
    display: flex;
    justify-content: space-around;
}

.node {
    border-radius: 50%;
    border: 1px solid black;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
}

.fillerNode {
    border-radius: 50%;
    border: none;
}
